import React, {Component} from 'react';
import '../App.css';

class India extends Component{
    constructor(props){
        super(props);
        this.state = {
            results: [],
            received: false
        };
    }

    async fetchFirst(){
        const response = await fetch("https://api.covid19india.org/data.json");
        const data = await response.json();
        console.log(data);
        this.setState({results: data, received: true});
        console.log(this.state.results);
    }

    componentDidMount(){
        this.fetchFirst();
    }
    render() {
        return(
            
            <div>
                { this.state.received ?
                    <table className="india mt-3">
                        <thead>
                            <tr>
                                <th colSpan='4'  className = "table-heading">INDIA</th>
                            </tr>
                            <tr>
                                <td>CASES</td>
                                <td>ACTIVE</td>
                                <td>DEATHS</td>
                                <td>RECOVERED</td>
                            </tr>
                        </thead>

                        <tbody>
                            <tr>
                                <td><span className="badge badge-pill badge-primary">{this.state.results.statewise[0].confirmed}</span></td>
                                <td><span className="badge badge-pill badge-success">{this.state.results.statewise[0].active}</span></td>
                                <td><span className="badge badge-pill badge-danger">{this.state.results.statewise[0].deaths}</span></td>
                                <td><span className="badge badge-pill badge-warning">{this.state.results.statewise[0].recovered}</span></td>
                            </tr>
                        </tbody>
                        
                        <thead>
                            <tr>
                                <td colSpan='2' className="innerTable">TODAY CASES</td>
                                <td colSpan='2' className="innerTable">TODAY DEATHS</td>
                            </tr>
                        </thead>

                        <tbody>
                            <tr>
                                <td colSpan='2'><span className="badge badge-primary">{this.state.results.statewise[0].deltaconfirmed}</span></td>
                                <td colSpan='2'><span className="badge badge-danger">{this.state.results.statewise[0].deltadeaths}</span></td>
                            </tr>
                        </tbody>
                    </table>
                    :
                    <div className="loading"> Loading...  <i className="fa fa-spinner fa-spin" ></i> </div>
                }
            </div>
        );
    }
}
export default India;