import React, {Component} from 'react';

class Country extends Component {

  constructor (props) {
    super(props);

    this.state = {
      results: [], 
      received: false
    }
  }

  async search(){

    const country = this.props.countryName;
    console.log("CDM " + country);

    if(country != null) {
      const response = await fetch("https://disease.sh/v2/countries/" + country);
      const data = await response.json();
      console.log(data);
      this.setState({results: data, received: true});
      console.log(this.state.results);
    }
  } 
  
 
  render () {

    this.search();

    console.log(this.props.countryName);
    
    return (
      <div>
        { this.state.received ?
            <div>
              <table className="countrySearchTable ">
                <thead>
                    <tr>
                      <th colSpan='4' className="table-heading"> {this.props.countryName} </th>
                    </tr>
                    <tr>
                      <td>CASES</td>
                      <td>ACTIVE</td>
                      <td>DEATHS</td>
                      <td>RECOVERED</td>
                      </tr>
                </thead>
                <tbody>
                  <tr>
                    <td><span className="badge badge-pill badge-primary">{this.state.results.cases}</span></td>
                    <td><span className="badge badge-pill badge-success">{this.state.results.active}</span></td>
                    <td><span className="badge badge-pill badge-danger">{this.state.results.deaths}</span></td>
                    <td><span className="badge badge-pill badge-warning">{this.state.results.recovered}</span></td>
                  </tr>
                </tbody>
                <thead>
                    <tr>
                      <td colSpan='2' className="innerTable">TODAY CASES</td>
                      <td colSpan='2' className="innerTable">TODAY DEATHS</td>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                      <td colSpan='2'><span className="badge badge-pill badge-primary">{this.state.results.todayCases}</span></td>
                      <td colSpan='2'><span className="badge badge-pill badge-danger">{this.state.results.todayDeaths}</span></td>
                    </tr>
                </tbody>
            </table>
            </div>
            :
            <div>
            </div>
        }
      </div>
    );
  }
}

export default Country;