import React, {Component} from 'react';
class StateTable extends Component{


    renderTable(stateData){
        let table = [];

        for(let i = 1; i < stateData.length; i++){
            
            let row = [];
            let stateName = stateData[i].state;

            if(stateName !== "State Unassigned"){

                row.push(<td className="state_name">{stateData[i].state} <i className=" fa fa-hand-pointer-o ml-1"></i></td>);
                row.push(<td>{stateData[i].active}</td>);
                row.push(<td>{stateData[i].confirmed}</td>);
                row.push(<td>{stateData[i].deaths}</td>);
                row.push(<td>{stateData[i].recovered}</td>);
                table.push(<tr key={stateData[i].state}  onClick={() => this.props.onStateSelect(stateName)}>{row}</tr>);
            }
        }

        return table;
    }

    render() {

        console.log(this.props.stateData);
        console.log(this.props.stateData.length);

        return(
            <div>
                    <table className="stateTable">
                        <thead>
                            <tr>
                            <th><span className="badge badge-pill badge-dark">State / UT</span></th>
                            <th><span className="badge badge-pill badge-success">Active Cases</span></th>
                            <th><span className="badge badge-pill badge-primary">Confirmed Cases</span></th>
                            <th><span className="badge badge-pill badge-danger">Total Deaths</span></th>
                            <th><span className="badge badge-pill badge-warning">Recovered</span></th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.renderTable(this.props.stateData)}
                        </tbody>
                    </table>
            </div>
        )
    }
}
    
export default StateTable;