import React, { Component } from "react";
import Chart from 'react-apexcharts';

class StateGraph extends Component {

    state = {

        options_all: {
            chart: {
                type: "line",
           
                stroke: {
                    curve: 'smooth'
                }
            },
          
            title: {
                text: "",
                align: "center",
            },

            xaxis: {
                categories: [],
            },
        },


        options_total: {
            chart: {
                type: "line",
           
                stroke: {
                    curve: 'smooth'
                }
            },
          
            title: {
                text: "",
                align: "center",
            },

            xaxis: {
                categories: [],
            },
        },

        options_active: {
            chart: {
                type: "line",
           
                stroke: {
                    curve: 'smooth'
                }
            },
          
            title: {
                text: "",
                align: "center",
            },

            xaxis: {
                categories: [],
            },
        },

        options_deaths: {
            chart: {
                type: "line",
           
                stroke: {
                    curve: 'smooth'
                }
            },
          
            title: {
                text: "",
                align: "center",
            },

            xaxis: {
                categories: [],
            },
        },

        options_recovered: {
            chart: {
                type: "line",
           
                stroke: {
                    curve: 'smooth'
                }
            },
          
            title: {
                text: "",
                align: "center",
            },

            xaxis: {
                categories: [],
            },
        },
        All:[
            {
                name: "",
                data: [],
            }
        ],
        total: [
            {
                name: "",
                data: [],
            },
        ],

        deaths: [
            {
                name: "",
                data: [],
            },
        ],

        active: [
            {
                name: "",
                data: [],
            },
        ],

        recovered: [
            {
                name: "",
                data: [],
            },
        ],
    };

    componentDidMount(){
        var data = this.props.oldData;
        var selectedData = this.props.selectedColumn;
        var confirmed = [];
        var dates = [];
        var deaths = [];
        var recovered = [];
        var active = [];
        
        console.log(selectedData);
        
        for(var i = data.length - 22; i < data.length; i++){
            dates.push(data[i].date);
            confirmed.push( parseInt( data[i].totalconfirmed) );
            deaths.push( parseInt(data[i].totaldeceased) );
            recovered.push( parseInt(data[i].totalrecovered) );
            var ac = parseInt(data[i].totalconfirmed) - (parseInt(data[i].totaldeceased) + parseInt(data[i].totalrecovered));
            active.push( parseInt(ac));

        }

        this.setState({

            options_all: {
                
                colors: ['#0000ff', '#ff0000', '#ffdf00', '#228B22' ],
                chart: {
                    type: "line",
                    
                    toolbar: {
                        show: false
                    }  
                },

                stroke: {
                    curve: "smooth",
                    width: [5, 5, 5, 5],
                    dashArray: [0, 0, 0, 0]
                },

                markers: {
                    size: 1,
                    shape: "circle",
                    fillColor: '#f7f4f3',
                },

                dataLabels: {
                    enabled: true,
                    style: {
                        colors: ['#4169e1', 'rgba(240, 24, 24, 0.938)', '#ffff00', '#76BA1B' ]
                    },
                    background:{
                        foreColor: 'black'
                        
                    }
                },
    
                title: {
                    margin: 20,
                },
                xaxis: {
                    categories: dates
                },
                legend: {
                    position: 'top',
                    horizontalAlign: 'right',
                    offsetY: -10,
                    offsetX: -5,
                },
                
            },


            options_total: {

                colors:['#0000ff'],
                
                chart: {
                    type: "area",
                    
                    toolbar: {
                        show: false
                    }  
                },
                
                stroke: {
                    curve: "smooth",
                },

                fill: {
                    colors: ['#0000ff']
                },
    
                dataLabels: {
                    enabled: false,
                },
    
                xaxis: {
                    categories: dates
                },
                title: {
                    text: "Last 20+ days Confirmed Cases",
                    align: "center",
                    margin: 20,
                    style: {
                        fontsize: "30px",
                        color: "#0000ff"
                    }
                    
                }

            },

            options_deaths: {

                colors:['#ff0000'],
                
                chart: {
                    type: "area",
                    
                    toolbar: {
                        show: false
                    }  
                },

                stroke: {
                    curve: "smooth",
                },

                fill: {
                    colors: ['#ff0000']
                },

                dataLabels: {
                    enabled: false,
                },
    
                xaxis: {
                    categories: dates
                },
                title: {
                    text: "Last 20+ days deaths",
                    align: "center",
                    margin: 20,
                    style: {
                        fontsize: "30px",
                        color: "#ff0000"
                    }
                    
                }
    
            },

            options_active: {

                 colors:['#228B22'],
                
                chart: {
                    type: "area",
                    
                    toolbar: {
                        show: false
                    }  
                },

                stroke: {
                    curve: "smooth",
                },

                fill: {
                    colors: ['#00ff00']
                },
                dataLabels: {
                    enabled: false,
                },
    
                xaxis: {
                    categories: dates
                },
                title: {
                    text: "Last 20+ days active Cases",
                    align: "center",
                    margin: 20,
                    style: {
                        fontsize: "30px",
                        color: "#228B22"
                    }
                    
                },
                
            },

            options_recovered: {

                colors:['#ffdf00'],
                
                chart: {
                    type: "area",
                    
                    toolbar: {
                        show: false
                    }  
                },

                stroke: {
                    curve: "smooth",
                },

                fill: {
                    colors: ['#ffff00']
                },

                dataLabels: {
                    enabled: false,
                },
    
                xaxis: {
                    categories: dates
                },
                title: {
                    text: "Last 20+ days recovered Cases",
                    align: "center",
                    margin: 20,
                    style: {
                        fontsize: "30px",
                        color: "#ffdf00"
                    }
                    
                },

            },

            All: [
                    {
                        name: "Total Cases",
                        data: confirmed
                    },

                    {
                        name: "Total Deaths",
                        data: deaths
                    },

                    {
                        name: "Recoverd",
                        data: recovered
                    },

                    {
                        name: "Active",
                        data: active
                    }
                 ],

         total : [
                    {
                        name: "Total Cases",
                        data: confirmed
                    }
                 ],

         active : [
                    {
                        name: "Active Cases",
                        data: active
                    }
                 ],

         deaths : [
                    {
                        name: "Deaths",
                        data: deaths
                    }
                  ],

        recovered : [
                    {
                        name: "Recovered",
                        data: recovered
                    }
                  ]


        });
        
    }

    render(){   

        return(
            <div className = "graph">
                <Chart 
                    options={this.state.options_all} 
                    series={this.state.All}
                    type = "line"
                    height = '500px'
                 />
                 
                  <Chart 
                    options={this.state.options_total} 
                    series={this.state.total}
                    type="area"
                    height = '335px'
                    
                 />

                  <Chart 
                    options={this.state.options_active} 
                    series={this.state.active}
                    type="area"
                    height = '335px'
                    
                 />

                  <Chart 
                    options={this.state.options_deaths} 
                    series={this.state.deaths}
                    type="area"
                    height = '335px'
                    
                 />

                   <Chart 
                    options={this.state.options_recovered} 
                    series={this.state.recovered}
                    type="area"
                    height = '335px'
                    
                 />


            </div>
        );
    }
}

export default StateGraph;