import React, {Component} from 'react';

class WorldTable extends Component{

    render() {

        let active = this.props.Global.TotalConfirmed - (this.props.Global.TotalDeaths + this.props.Global.TotalRecovered);

        return(
            <div>
              <table className="global-table">
                <thead>
                    <tr>
                        <th colSpan='4' className = "table-heading"> GLOBAL </th>
                    </tr>
                    <tr>
                        <td>CASES</td>
                        <td>ACTIVE</td>
                        <td>DEATHS</td>
                        <td>RECOVERED</td>
                        </tr>
                </thead>
                <tbody>
                    <tr>
                        <td><span className="badge badge-pill badge-primary">{this.props.Global.TotalConfirmed}</span></td>
                        <td><span className="badge badge-pill badge-success">{active}</span></td>
                        <td><span className="badge badge-pill badge-danger">{this.props.Global.TotalDeaths}</span></td>
                        <td><span className="badge badge-pill badge-warning">{this.props.Global.TotalRecovered}</span></td>
                    </tr>
                </tbody>
                <thead>
                    <tr>
                        <td colSpan='2' className="innerTable">TODAY CASES</td>
                        <td colSpan='2' className="innerTable">TODAY DEATHS</td>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td colSpan='2'><span className="badge badge-pill badge-primary">{this.props.Global.NewConfirmed}</span></td>
                        <td colSpan='2'><span className="badge badge-pill badge-danger">{this.props.Global.NewDeaths}</span></td>
                    </tr>
                </tbody>
            </table>
        
            </div>
        );
    }
}
export default WorldTable;